import React, { useState, useEffect } from 'react';
import './hero.css';
import image from '../../images/after.png';
import { FaCheckCircle } from 'react-icons/fa';

// Submit to Google Form
async function submitGoogleForm({
  name,
  phone,
  email,
  message,
  pagePath,
  campaign,
  id,
}: {
  name: string;
  phone: string;
  email: string;
  message: string;
  pagePath: string;
  campaign: string;
  id: string;
}) {
  const formUrl = `https://docs.google.com/forms/d/e/${id}/formResponse`;

  const formData = new URLSearchParams();
  formData.append('entry.466204223', name);
  formData.append('entry.32405555', phone);
  formData.append('entry.2122071872', email);
  formData.append('entry.176904452', message);
  formData.append('entry.1591426887', pagePath);
  formData.append('entry.688569624', campaign);

  try {
    await fetch(formUrl, {
      method: 'POST',
      mode: 'no-cors',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: formData.toString(),
    });
    return true;
  } catch (error) {
    console.error('Error submitting form:', error);
    return false;
  }
}

const testimonials = [
  {
    id: 1,
    text: "Bravo Roofing offered a professional and timely service. They accomplished the teardown of our very steep roof protecting both our house and vegetation.",
    author: "Bill & Nancy, Garden City 83714",
    stars: 5,
  },
  {
    id: 2,
    text: "The team at Bravo Roofing was courteous and quick. They removed our old roof safely and left no mess behind. Everything was completed exactly as promised and on schedule.",
    author: "Mike D., Meridian",
    stars: 5,
  },
  {
    id: 3,
    text: "Bravo Roofing showed up on time and worked efficiently. Their attention to detail and care for our landscaping really stood out. We‚Äôre impressed with the final result!",
    author: "Rachel T., Eagle ID",
    stars: 5,
  },
];

const TestimonialCarousel = ({ isPaused }: { isPaused: boolean }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    if (isPaused) return;
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % testimonials.length);
    }, 4000);
    return () => clearInterval(interval);
  }, [isPaused]);

  const renderStars = (count: number) =>
    Array.from({ length: count }, (_, i) => <span key={i} className="star">‚≠ê</span>);

  return (
    <div className="testimonial-carousel">
      <div className="testimonial-content">
        <div className="stars">{renderStars(testimonials[currentIndex].stars)}</div>
        <p className="testimonial-text">"{testimonials[currentIndex].text}"</p>
        <p className="testimonial-author">‚Äî {testimonials[currentIndex].author}</p>
      </div>
      <div className="carousel-dots">
        {testimonials.map((_, index) => (
          <button
            key={index}
            className={`dot ${index === currentIndex ? 'active' : ''}`}
            onClick={() => setCurrentIndex(index)}
            aria-label={`Go to testimonial ${index + 1}`}
          />
        ))}
      </div>
    </div>
  );
};

const ContactForm = ({ onFocus, onBlur }: { onFocus: () => void; onBlur: () => void }) => {
  const [formData, setFormData] = useState({ name: '', phone: '', email: '', message: '' });
  const [showTooltip, setShowTooltip] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const ok = await submitGoogleForm({
      id: '1FAIpQLSdoSJv57tLdEFGNhvJKIvQe6vlIRknTGqCXnT21hjU8H1dSDg',
      ...formData,
      pagePath: window.location.pathname,
      campaign: 'bravo-roofing-lead',
    });

    if (ok) {
      setFormData({ name: '', phone: '', email: '', message: '' });
      setShowTooltip(true);
      setTimeout(() => setShowTooltip(false), 5000);
    } else {
      alert("Something went wrong. Please try again.");
    }
  };

  return (
    <div className="contact-form-container">
      <h3 className="form-title">Get a Free Roof Estimate</h3>
      <form className="contact-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <input type="text" name="name" placeholder="Full Name" value={formData.name} onChange={handleChange} onFocus={onFocus} onBlur={onBlur} required />
        </div>
        <div className="form-group">
          <input type="tel" name="phone" placeholder="Best Phone Number to Reach You" value={formData.phone} onChange={handleChange} onFocus={onFocus} onBlur={onBlur} required />
        </div>
        <div className="form-group">
          <input type="email" name="email" placeholder="Your Email Address" value={formData.email} onChange={handleChange} onFocus={onFocus} onBlur={onBlur} required />
        </div>
        <div className="form-group">
          <textarea name="message" placeholder="Tell us what you need (e.g. roof leak, replacement)" value={formData.message} onChange={handleChange} onFocus={onFocus} onBlur={onBlur} rows={4} />
        </div>
        <button type="submit" className="submit-btn btn-primary">
          <span className="btn-icon">üìã</span>
          <span className="btn-text">Get My Free Estimate</span>
          <div className="btn-shine"></div>
        </button>
      </form>
      {showTooltip && (
        <div className="hipo-success-message">
          <FaCheckCircle className="hipo-success-icon" />
          Thank you! We‚Äôll get back to you shortly.
        </div>
      )}
    </div>
  );
};

